.navbar {
    position: fixed;
    top: 0;
    right: 0;
    padding: 20px;
    background-color: #0d071f;
    display: flex;
    justify-content: space-around;
    width: 100%;
    z-index: 1000;
  }
  
  .navbar a {
    color: white;
    text-decoration: none;
    font-size: 1.2rem;
    margin: 0 15px;
    cursor: pointer;
  }
  
  .navbar a:hover {
    color: #007bff;
  }
  
  .navbar .active {
    border-bottom: 2px solid #007bff;
    padding-bottom: 8px;
    color: #007bff;
  }
  